.App {
  text-align: center;
}

table {
  border-spacing: 0;
  border: 1px solid black;
  margin-left: auto;
  margin-right: auto;
}

tr {
  border-top: 1px solid #c6cbd1;
  background: transparent; 
}

th,
td {
  padding: 6px 13px;
  border: 1px solid black;
  width: 150px;
}

.MuiTableCell-root {
  border-bottom: none;
  /* border-right: none;
  border-left: none; */
}

.MuiTableCell-root :last-child {
  border-right: none;
  border-left: none;
}
